.player {
  display: flex;
  flex-direction: column;
  position: relative;
  border-radius: var(--round-0);
  --light-overlay: rgba(95, 95, 95, 0.322);
  overflow: hidden;
  position: sticky;
  top: 0;
  box-shadow: var(--shadow-all);
}
.hide-cursor {
  /* cursor: none; */
  /* border: 3px solid blue; */
}
.player video {
  min-width: 100%;
  background: #000;
  border-radius: inherit;
  height: 100%;
}


.player .controls {
  position: absolute;
  bottom: 0;
  left: 0;
  width: 100%;
  display: flex;
  border-radius: inherit;
  border-top-left-radius: 0;
  border-top-right-radius: 0;
  padding: 5px 5px 15px;
  flex-direction: column;
  transition: 0.4s ease-in;
  background-color: var(--light-overlay);
}

.controls.hidden {
  visibility: hidden;
  transform: translateY(100%);
}

.player .controls .btns {
  display: flex;
  align-items: center;
  justify-content: space-between;
}

.progress-wrapper {
  /* flex: 50%; */
  width: 100%;
  height: 6px;
  background: var(--bg-opacity-3);
  border-radius: var(--round-0);
  cursor: pointer;
}

.progress-wrapper .progress-line {
  width: 0;
  height: inherit;
  border-radius: inherit;
  background-color: white;
  /* var(--bg-opacity-2); */
  position: relative;
}

.progress-wrapper .progress-line::after {
  content: '';
  position: absolute;
  width: 10px;
  height: 10px;
  background-color: inherit;
  top: 50%;
  right: 0;
  border-radius: 50%;
  transform: translate(50%, -50%);
  opacity: 0;
  /* pointer-events: none; */
  transition: 0.3s;
}
.progress-wrapper:hover .progress-line::after {
  opacity: 1;
}

/* video overlay */
.video-overlay {
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  /* background-color: var(--light-overlay); */
  display: flex;
  justify-content: center;
  align-items: center;
  transition: .3s ease-in;
}

.video-overlay .icon {
  height: 70px;
  width: 70px;
  box-shadow: var(--shadow-all-bigger);
  border: 3px solid var(--bg-opacity);
  backdrop-filter: blur(2px) saturate(2);
  -moz-backdrop-filter: blur(2px) saturate(2);
}

.video-overlay .icon .material-icons {
  font-size: 2rem;
}

.video-overlay.hidden {
  opacity: 0;
}

.controls .right,
.controls .right .volume-wrapper {
  display: flex;
  align-items: center;
  overflow-x: hidden;
  border-radius: var(--round-1);
  /* padding: 1px 7px; */
}

.controls .middle h6 {
  /* color: black; */

  text-shadow: 0 3px 2px black;
}

.controls .volume-wrapper button {
  width: auto;
  border-radius: calc(var(--round-1) * 2);
  height: 30px;
}

.controls .volume-wrapper button .progress-wrapper {
  visibility: hidden;
  width: 50px;
  transition: 0.4s;
  margin-right: 2px;
  transition: all 0.4s ease;
  transform: translateX(50%);
  opacity: 0;
}

.controls .volume-wrapper button:hover .progress-wrapper {
  opacity: 1;
  visibility: visible;
  transform: translateX(0);
}


