@import "./styles/material-icons/material-icons.css";
@import "./styles/player.css";

:root {
  --bg-color: #202020;
  --bg-opacity: #464646b2;
  --bg-opacity-2: #524f4fb2;
  --bg-opacity-3: #00000091;
  --text-color: #dbdbdb;
  --round-0: 5px;
  --round-1: 10px;
  /* --shadow-top: 1px 2px 7px var(--bg-opacity); */
  --shadow-big: 0 0 4px 1rem var(--bg-opacity);
  --shadow-top: 1px -2px 4px var(--bg-opacity);
  --shadow-all-bigger: 0 0 7px 2px var(--bg-opacity-3);
  --shadow-all: 0 0 4px 1px var(--bg-opacity);
  --shadow-bottom: 1px 1px 4px var(--bg-opacity);
}

*, *::after, *::before {
  margin: 0;
  padding: 0;
  outline: none;
  box-sizing: border-box;
}

body, #root {
  /* fonts */
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;

  /* colors */
  background-color: var(--bg-color);
  color: var(--text-color);

  /* layout */
  min-height: 100vh;
  display: flex;
  flex-direction: column;
}

/* globals */

ul, ol {
  list-style: none;
}

/* links and buttons */
a {
  text-decoration: none;
  color: inherit;
  text-align: center;
  vertical-align: middle;
}

a.icon, button.icon {
  border-radius: 50%;
  height: 40px;
  width: 40px;
  text-align: center;
}

a, button {
  vertical-align: middle;
  border-radius: 5px;
  border: 0;
  padding: 5px 10px;
  margin: 2px 1px;
  cursor: pointer;
  background-color: inherit;
  color: inherit;
  display: flex;
  justify-content: center;
  align-items: center;
  transition: .3s ease;
}

a:hover, button:hover {
  background-color: var(--bg-opacity-2);
}

a:focus, button:focus {
  animation: animate-shadow .2s ease;
  background-color: var(--bg-opacity-3);
  /* transform: scale(1.05); */
}

/* navigation */
nav {
  display: flex;
  position: sticky;
  top: 0;
  z-index: 2;
  justify-content: space-around;
  padding: 5px 80px;
  box-shadow: var(--shadow-bottom);
  background-color: var(--bg-color);
  align-items: center;
}

nav > * {
  flex: 50%;
}

nav ul {
  display: flex;
  justify-content: space-around;
  align-items: center;
}

nav ul li a {
  padding: 5px 12px;
  line-height: 1.5rem;
}
nav .logo {
  display: flex;
  align-items: center;
  font-size: xx-large;
  font-weight: 900;
  color: transparent;
  background-image: linear-gradient(yellow, lightgreen, rgb(215, 220, 224));
  background-clip: text;
  -webkit-background-clip: text;
}

/* main */

main {
  flex-grow: 1;
  padding: 2rem;
  display: grid;
  grid-template-columns: 1.5fr 1fr;
  gap: 2rem;
  justify-content: center;
  align-items: flex-start;
  position: relative;
}

/* footer */

footer {
  padding: 20px;
  text-align: center;
  background-color: var(--bg-opacity);
  box-shadow: var(--shadow-top);
  position: absolute;
  display: none;
}

/* playlist */

.playlist {
  /* max-width: 420px; */
  background-color: var(--bg-opacity);
  padding: 7px 10px;
  border-radius: var(--round-0);
  box-shadow: var(--shadow-all);
  display: flex;
  flex-direction: column;
  position: sticky;
  top: 0;
}

.playlist > *:not(.tracks){
    position: sticky;
    top: 5px;
    background-color: var(--bg-opacity-3);
}

.playlist h1 {
  margin: 8px 0;
  line-height: 2.5rem;
  text-align: center;
}

.playlist .playlist-btns {
    display: flex;
}

.playlist .tracks {
  display: flex;
  flex-direction: column;
}

/* track */
.track {
  cursor: pointer;
  display: flex;
  width: 100%;
  align-items: center;
  justify-content: space-between;
  background-color: var(--bg-opacity-2);
  border-radius: var(--round-0);
  padding: 4px 10px;
  margin: 2px 0;
}

.track-btns {
  display: flex;
  margin: 0 1.5px;
}

.track-btns button {
  margin: 0 1.5px;
  padding: 2.4px;
  border-radius: 100%;
}

.track-btns button .material-icons {
  font-size: 17px;
}

.track .track-name {
  word-break: break-all;
  letter-spacing: .2px;
  font-family: cursive, sans-serif;
}

.track.playing {
    border: 2px solid var(--bg-color);
    box-shadow: var(--shadow-all-bigger);
}

.track-btns button.play {
  color: #1e8fffd2;
} 
.track-btns button.delete {
  color: #fc2c1def;
} 



/* anamations */

@keyframes animate-shadow {
  from {
    box-shadow: var(--shadow-all);
  }
  to {
    box-shadow: var(--shadow-big);
  }
}